.experience-section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: center;
    position: relative;
    align-items: center;
    gap: 0.5rem;

    &__content {
        display: flex;
        flex-direction: column;
    }

    &__total-exp {
        font-size: 0.75rem;
        line-height: 1.33;
        margin-bottom: 8px;
    }

    &__role-name {
        font-size: 0.9375rem;
        font-weight: 500;
        line-height: 1.33;
    }

    &__role-period {
        font-size: 0.875rem;
        font-weight: 300;
        line-height: 1.33;
        margin-bottom: 8px;
    }

    .showcase-card {
        @include breakpoint(medium) {
            flex-basis: 23%;
        }
    }
}
