.footer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: #E94560;
    padding: 15px 30px;

    @include breakpoint(medium) {
        justify-content: flex-end;
    }

    &__copy-right-text {
        font-size: .75rem;
        color: $grey-f0;
    }
}
