@mixin breakpoint($class) {
    @if $class==small {
        @media (min-width: 0rem) and (max-width: 47.9375rem) {
            @content;
        }
    } @else if $class==medium {
        @media (min-width: 48rem) {
            @content;
        }
    } @else if $class==medium-xlarge {
        @media (min-width: 48rem) and (max-width: 72rem) {
            @content;
        }
    } @else if $class==large {
        @media (min-width: 64rem) {
            @content;
        }
    } @else if $class==xlarge {
        @media (min-width: 72rem) {
            @content;
        }
    } @else if $class==xxlarge {
        @media (min-width: 86rem) {
            @content;
        }
    } @else {
        @warn "Breakpoint mixin supports: small, medium, medium-xlarge, large, xlarge, xxlarge";
    }
}
